.apiConfigContainer {
  padding-left: 74px;
  padding-right: 16px;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  min-height: 100vh;
  overflow-y: auto;
}

.apiConfigContainer h2.user-path {
  margin: 0;
  padding: 0;
  color: #4a88b1;
  font-size: 14px;
  font-weight: normal;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.user-path-title {
  font-weight: bold;
  color: #185177;
}

.custom-codeSnippet {
  margin-bottom: 50px;
}

.stepsSection {
  display: flex;
  align-items: flex-start;
  gap: 10px;
}

.stepsList {
  margin: 0;
}

.pin-logo {
  width: 19px;
  height: 19px;
}
