.deploymentContainer h1 {
  font-size: 18px;
}

.deploymentContainer h2 {
  font-size: 14px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-top: 100px;
  margin-top: -80px;
}

.deploymentContainer ul li {
  font-size: 14px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  line-height: 19px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.deploymentContainer a {
  color: rgb(15 91 229);
  text-decoration: none;
}

@media (max-width: 800px) {
  .deploymentContainer {
    padding-right: 16px;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
  }
}

@media (max-width: 500px) {
  .deploymentContainer {
    padding-right: 16px;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
  }
}
